<script>
export default {
  name: 'OperatingAttendance',
  data() {
    return {
			// 搜索表单规则
      searchValidateFormRules: {},
      searchScenicOptions: [], // 搜索的景区选项
      searchValidateForm: {}, // 搜索表单验证对象
			General:[]//概况
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }
    
    this.getSearchValidateForm()
    this.getScenicList()
    this.initTableData()
  },
  beforeMount() {},
  mounted() {},
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm,
        companyId: '', // 归属公司id
        scenicSpotId: '' // 景区id
      }
    },
    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },		
		// DetailsStaff(){
		// 	window.sessionStorage.setItem('empAtt',1)
		// },
		// robotDetails(){
		// 	window.sessionStorage.setItem('roboDeta',1)
		// },
    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
				this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
		///page/assetsSystem/operatingAttendance/
    async initTableData() {
      this.tableLoading = true
      const { data: res } = await this.$http.get(
        '/sys/AttendanceRecord/selectSurvey',
        {
          params: {
            ...this.searchValidateForm,
          }
        }
      )
      console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
			console.log(res.data)
      this.General = res.data
      this.tableLoading = false
			console.log(this.General)
    },
		
    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(this.searchValidateForm);
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
  }
}
</script>

